/**
 * Environment Configuration for TDnet Data Collector
 * 
 * This file defines environment-specific configurations for Lambda functions
 * to optimize resource allocation and logging based on the deployment environment.
 */

/**
 * Environment type
 */
export type Environment = 'dev' | 'prod';

/**
 * Lambda function configuration interface
 */
export interface LambdaEnvironmentConfig {
  /**
   * Function timeout in seconds
   */
  timeout: number;

  /**
   * Memory size in MB
   */
  memorySize: number;

  /**
   * Log level (DEBUG, INFO, WARN, ERROR)
   */
  logLevel: 'DEBUG' | 'INFO' | 'WARN' | 'ERROR';
}

/**
 * All Lambda functions configuration
 */
export interface EnvironmentConfig {
  /**
   * Environment name
   */
  environment: Environment;

  /**
   * Collector Lambda configuration
   */
  collector: LambdaEnvironmentConfig;

  /**
   * Query Lambda configuration
   */
  query: LambdaEnvironmentConfig;

  /**
   * Export Lambda configuration
   */
  export: LambdaEnvironmentConfig;

  /**
   * Collect Lambda configuration (API endpoint)
   */
  collect: LambdaEnvironmentConfig;

  /**
   * Collect Status Lambda configuration (API endpoint)
   */
  collectStatus: LambdaEnvironmentConfig;

  /**
   * Export Status Lambda configuration (API endpoint)
   */
  exportStatus: LambdaEnvironmentConfig;

  /**
   * PDF Download Lambda configuration (API endpoint)
   */
  pdfDownload: LambdaEnvironmentConfig;
}

/**
 * Development environment configuration
 */
export const devConfig: EnvironmentConfig = {
  environment: 'dev',
  collector: {
    timeout: 300, // 5 minutes
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
  query: {
    timeout: 10, // 10 seconds
    memorySize: 128, // 128 MB
    logLevel: 'DEBUG',
  },
  export: {
    timeout: 120, // 2 minutes
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
  collect: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
  collectStatus: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
  exportStatus: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
  pdfDownload: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'DEBUG',
  },
};

/**
 * Production environment configuration
 */
export const prodConfig: EnvironmentConfig = {
  environment: 'prod',
  collector: {
    timeout: 900, // 15 minutes
    memorySize: 512, // 512 MB
    logLevel: 'INFO',
  },
  query: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'INFO',
  },
  export: {
    timeout: 300, // 5 minutes
    memorySize: 512, // 512 MB
    logLevel: 'INFO',
  },
  collect: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'INFO',
  },
  collectStatus: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'INFO',
  },
  exportStatus: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'INFO',
  },
  pdfDownload: {
    timeout: 30, // 30 seconds
    memorySize: 256, // 256 MB
    logLevel: 'INFO',
  },
};

/**
 * Get environment configuration based on environment name
 * 
 * @param env - Environment name ('dev' or 'prod')
 * @returns Environment configuration
 */
export function getEnvironmentConfig(env: Environment): EnvironmentConfig {
  switch (env) {
    case 'dev':
      return devConfig;
    case 'prod':
      return prodConfig;
    default:
      throw new Error(`Unknown environment: ${env}`);
  }
}
